import React, { useMemo, useState } from "react";
import classNames from "classnames";
import techSkillLogo from '../../Images/tech-skill-logo.png';
import langSkillLogo from '../../Images/lang-skill-logo.png';

const skillsDesc = {
  tech: "Good knowledge of front-end basics - HTML, CSS, JavaScript and preprocessor Sass. Experience with React, Redux and additional libraries (react-router, redux-thunk etc. ). Also I worked with git/github and Jira.",
  lang: " I know two languages: Ukrainian - is my native language, and English - I have a level between B1 and B2."
};
const circleText = {
  bigCircleTitle: 'LANGUAGES',
  bigCircleSkills: 'HTML CSS SASS JS REACT REDUX',
  smallCircleTitle: 'TECHNICAL',
  smallCircleSkills: 'English      Ukrainian',
}
const Skills = () => {
  const [currSkills, setShowSkills] = useState('tech');
  const [isMouseClicked, setIsMouseClicked] = useState(0);

  const skillToggle = (skill) => {
    if(skill === currSkills) {
      return
    }
    setIsMouseClicked((prev) => ++prev);
    setShowSkills(skill);
  };
  const bendText = (str, k) => str.split('').map((chr, idx) => (
    <span key={idx} style={{transform: `rotate(${idx * k}deg)`}}>{chr}</span>
  ));
  const bigCircleClass = classNames({
    'circle-tech--outer': true,
    'big-circle--active': isMouseClicked && currSkills === 'tech',
    'big-circle--passive': isMouseClicked && currSkills === 'lang',
  });
  const smallCircleClass = classNames({
    'circle-tech--inner': true,
    'small-circle--active': isMouseClicked && currSkills === 'lang',
    'small-circle--passive': isMouseClicked && currSkills === 'tech',
  });
  const techSkillLogoStyle = classNames({
    'tech-skill-logo': true,
    'circle-logo--active': isMouseClicked && currSkills === 'tech',
    'circle-logo--passive': isMouseClicked && currSkills === 'lang',
  });
  const langSkillLogoStyle = classNames({
    'lang-skill-logo': true,
    'circle-logo--active': isMouseClicked && currSkills === 'lang',
    'circle-logo--passive': isMouseClicked && currSkills === 'tech',
  })
  const techSkillTitleStyle = classNames({
    'skills-title': true,
    'skills-title--active': isMouseClicked && currSkills === 'tech',
    'skills-title--passive': isMouseClicked && currSkills === 'lang',
  })
  const langSkillTitleStyle = classNames({
    'skills-title': true,
    'skills-title--active': isMouseClicked && currSkills === 'lang',
    'skills-title--passive': isMouseClicked && currSkills === 'tech',
  })
  
  const tech = useMemo(() => bendText(circleText.bigCircleSkills, 6.42857142857), []);
  const langTitle = useMemo(() => bendText(circleText.bigCircleTitle, 6.42857142857), []);
  const lang = useMemo(() => bendText(circleText.smallCircleSkills, 6.42857142857), []);
  const techTitle = useMemo(() => bendText(circleText.smallCircleTitle, 6.42857142857), []);
  return (
    <div className="skills">
      <div className={bigCircleClass}>
        <p className="skills-list">{tech}</p>
        <p className={langSkillTitleStyle} onClick={() => skillToggle('lang')}>{langTitle}</p>
      </div>
      <div className={smallCircleClass}>
        <p className="skills-list">{lang}</p>
        <p className={techSkillTitleStyle} onClick={() => skillToggle('tech')}>{techTitle}</p>
        <div className="circle-lang">
          <img className={techSkillLogoStyle} src={techSkillLogo} />
          <img className={langSkillLogoStyle} src={langSkillLogo} />
        </div>
      </div>
      <div className="skill-desc">
        <p>
          {currSkills === 'tech' ? skillsDesc.tech : skillsDesc.lang}
        </p>
      </div>
      <h1 className="page-title">SKILLS</h1>
    </div>
  )
}
export default Skills;